.hero{
    height: 100vh;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    .container{
        height: 100%;
        scroll-snap-align: center;
        width: 80vw;
        display: flex;
        justify-content: space-between;
        gap: 5vw;
        
        
       
        .left{
           flex:2;
           display: flex;
           flex-direction: column;
           justify-content: center;
           gap: 20px;
           h1{
            font-size: 60px;

           }
           h3{
            color: rgb(226, 7, 135);
            font-size: 25px;
            font-weight: 500;
           }
           p{
            font-size: 20px;
            color: lightgray;
           }
           button{
            background-color: rgb(226, 7, 135);
            color: white;
            font-weight: 500;
            width: 100px;
            padding: 10px;
            border: none;
            border-radius: 5px;
            cursor: pointer;
           }
            
        }
        .right{
            flex: 3;
            position: relative;
            
            img{
                width: 30vw;
                position: absolute;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
                margin: auto;
                animation: animate 3s infinite ease alternate;
                @keyframes animate{
                    to{
                        transform: scale(0.9) translateY(70px);
                    }
                }
                
            }
        }
    }
}